import React, { Component } from 'react';
import '../Styles/Project.css';

class Projects extends Component {



    triggerAnimationOnScroll() {
        let scrollPosition = window.pageYOffset;
        let windowHeight = document.documentElement.clientHeight;
        let elementHeight = document.getElementById('proj-wrapper').offsetHeight;
        let element = document.getElementById('proj-wrapper').offsetTop;
        if (scrollPosition > element - windowHeight && scrollPosition < element) {
            document.getElementById('project').style.cssText = "animation: project-animation 2s ease 0s 1 normal forwards;";
        }
        if (scrollPosition > element + elementHeight || scrollPosition < element - windowHeight) {
            document.getElementById('project').style.cssText = "animation: none";
        }
    }

    render() {
        window.addEventListener('scroll', this.triggerAnimationOnScroll)
        const { data } = this.props
        return (
            <section className="proj-section" id="proj-section" >
                <div className="proj-wrapper" id="proj-wrapper" >
                    <div className="project" id='project' >
                    <h2 className="title">{data.title}</h2>
                    <p className="subtitle">{data.subtitle}</p>
                        <div className="image-wrapper" >
                            <div className="hover-wrapper">
                                <a target="_blank" rel="noopener noreferrer" href="https://radoslaw13.github.io/osp-page-project/" className="live-demo">LIVE DEMO</a>
                                <a target="_blank" rel="noopener noreferrer" href="https://github.com/radoslaw13/osp-page-project" className="repo">REPOSITORY</a>
                            </div>
                        </div>
                        <div className="project-label-tech" >
                            <ul className='tech-list'> 
                                <li>ReactJS</li>
                                <li>React-Redux</li>
                                <li>React-Router</li>
                                <li>Material-UI</li>
                                <li>Parse Server</li>
                                <li>npm packages</li>
                            </ul>
                            <span className="project-label-tech-mobile">ReactJS/React-Redux/React-Router/Material-UI/Parse Server/npm packages</span>
                        </div>
                        <div className="project-label">
                            {data.label}
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default Projects;